.exposure-interface .skeleton-loader__bone {
  height: 18px;
  position: relative;
  background: var(--swap-loading-background-color, var(--swap-default-loading-background-color));
  border-radius: 10px;
  overflow: hidden;
}
.exposure-interface .skeleton-loader__bone:after {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));
  -webkit-animation: loading 1.5s infinite;
  animation: loading 1.5s infinite;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transform: translateX(-100%);
  z-index: 1;
}
.dark .exposure-interface .skeleton-loader__bone:after {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
}

