@-webkit-keyframes loading {
  0% {
    transform: translateX(-100%);
  }
  80% {
    transform: translateX(70%);
  }
  100% {
    transform: translate(100%);
  }
}

@keyframes loading {
  0% {
    transform: translateX(-100%);
  }
  80% {
    transform: translateX(70%);
  }
  100% {
    transform: translate(100%);
  }
}
.ReactCollapse--collapse {
  transition: height 500ms;
}

